import React from 'react';

class LoginsignIn extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      user: '',
      password: ''
    }
  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    });
  }

  handleSubmit = () => {
    /* Si es valido */
    /* Pasar el nombre del usuario al contexto global */
    alert("Submit form");
    this.singInUser();
  }

  singInUser = async () => {
    const URL_HEROKU = 'https://block-master-api.herokuapp.com/users';
    fetch(URL_HEROKU, {params: {user: this.state.user, password: this.state.password}})
      .then(response => response.data)
        .then(response => {
          console.log((response.lenght
            ? "Bienvenido " + response[0].nombre
            : "Usuario o Contraseña inválidos"
          ));
        })
        .catch(error => console.error("ERROR" + error));
  }

  render () {
    return (
      <div>
        <form onSubmit={this.handleSubmit}>
          <input
            type="email"
            name="user"
            value={this.state.user}
            onChange={this.handleChange}
            placeholder="Usuario"
            required
          />

          <input 
            type="password"
            name="password"
            value={this.state.user}
            onChange={this.handleChange}
            placeholder="Contraseña"
            required
          />

          <input type="submit" />
        </form>
      </div>
    )
  }
}

export default LoginsignIn;

